<template>
  <div class="row justify-content-md-center" id="translateForm">
    <div class="col-md-6 col-md-offset-3">
      <form id="transForm" v-on:submit="formSubmit">
        <input
          class="form-control myspace"
          type="text"
          v-model="textToTranslate"
          placeholder="Enter text to translate ..."
        />
        <select v-model="country_code" class="form-control myspace">
          <option disabled value selected>{{country}}</option>
          <option
            v-for="(country,country_code) in optionsObject"
            :key="country_code"
            v-bind:value="country_code"
          >{{country}}</option>
        </select>

        <input class="btn btn-primary myspace" :disabled="!textToTranslate" type="submit" value="Translate" />
      </form>
    </div>
  </div>
</template>

<script>

export default {
  name: "translateForm",
    props:['optionsObject'],
  data() {
    return {
      textToTranslate: "",
      country_code: ""
    };
  },
  created() {
    this.country_code = "ga";
    this.country = "Irish";
  },
  methods: {
    formSubmit(e) {
      this.$emit("formSubmit", this.textToTranslate, this.country_code);
      e.preventDefault();
    }
  }
};
</script>

<style>
#transForm {
  border-radius: 10px;
  border: 3px #ccc solid;
  padding: 5px;
}
.myspace {
  margin-top: 5px;
}
</style>



