<template>
  <div class="text-center" id="app">
    <h1>Vue Translate</h1>
    <h6 class="text-muted font-italic">Powered By Yandex API</h6>
    <hr />
    <TranslateForm v-on:formSubmit="translateText" v-bind:optionsObject="countryOptions"></TranslateForm>
    <TranslateOutput v-text="translatedResult"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from "./components/TranslateForm";
import TranslateOutput from "./components/TranslateOutput";

import { API, API_LANG, KEY } from "./variables.js";

export default {
  name: "App",
  components: {
    TranslateForm,
    TranslateOutput
  },
  created: function() {
    this.getCountryOptions();
  },
  data: function() {
    return {
      translatedResult: "",
      countryOptions: {}
    };
  },
  methods: {
    getCountryOptions: function(obj) {
      this.$http.get(API_LANG + "key=" + KEY + "&ui=en").then(response => {
        this.countryOptions = response.body.langs;
      }
      )
    },
    translateText: function(text, language) {
      //  alert(text);
      this.$http
        .get(API + "key=" + KEY + "&lang=" + language + "&text=" + text)
        .then(response => {
             console.log(response.status); // returns 200
          this.translatedResult = response.body.text[0];
          console.log(response.body.text[0]);
        });
    }
  }
};
</script>

<style>
body {
  background: #fefefe;
}
</style>



